"use strict";
const platform_browser_dynamic_1 = require('@angular/platform-browser-dynamic');
const app_component_ts_1 = require('./app/app.component.ts');
function main() {
    platform_browser_dynamic_1.bootstrap(app_component_ts_1.App, [])
        .catch(error => {
        console.error(error);
    });
}
exports.main = main;
document.addEventListener('DOMContentLoaded', () => main());



